<template>
	<div>
		<el-row>
		  <el-col :span="12">
			  <el-table :data="cookingTableData" style="width: 100%">
			  	<el-table-column style="font-size: 20px;" label="菜名" >
			  	  <template slot-scope="scope">
			  		  <el-tag style="font-size: 20px;">{{scope.row.footName}}</el-tag>
			  	  </template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="数量">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;">{{scope.row.num}}</el-tag>
			  		</template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="状态">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;" size="medium" v-if="scope.row.status==1">正常</el-tag>
			  			<el-tag style="font-size: 20px;" size="medium" v-if="scope.row.status==2">加菜</el-tag>
			  		</template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="备注">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;">{{scope.row.remark}}</el-tag>
			  		</template>
			  	</el-table-column>
			    </el-table>
		  </el-col>
		  <el-col :span="12">
			  <el-table :data="barbecueTableData" style="width: 100%">
			  	<el-table-column style="font-size: 20px;" label="菜名" >
			  	  <template slot-scope="scope">
			  		  <el-tag style="font-size: 20px;">{{scope.row.footName}}</el-tag>
			  	  </template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="数量">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;">{{scope.row.num}}</el-tag>
			  		</template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="状态">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;" v-if="scope.row.status==1">正常</el-tag>
			  			<el-tag style="font-size: 20px;" v-if="scope.row.status==2">加菜</el-tag>
			  		</template>
			  	</el-table-column>
			  	<el-table-column style="font-size: 20px;" label="备注">
			  		<template slot-scope="scope">
			  			<el-tag style="font-size: 20px;">{{scope.row.remark}}</el-tag>
			  		</template>
			  	</el-table-column>
			    </el-table>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	import {listOrderSkuNeedCooking} from '../../api/index.js'
	export default{
		data(){
			return {
				cookingTableData:[],
				barbecueTableData:[],
			}
		},
		methods:{
			getAllCookingOrderSku(){
				listOrderSkuNeedCooking().then(r=>{
					console.log(JSON.stringify(r.data))
					this.cookingTableData = r.data.cookingList
					this.barbecueTableData = r.data.barbecueList
				})
			}
		},
		created() {
			this.getAllCookingOrderSku()
		}
	}
</script>

